    dialogTitle() {
      if (this.editType === 'add') {
        return `新增${this.title}信息`
      } else {
        return `编辑${this.title}信息`
      }
    }
  },
  mounted() {
    this.init()
  },
  methods: {
    init(type) {
      this.loading = true
      const params = {
        ...this.query,
        supplierId: this.$route.params.id,
        condition: '2', // 代表详情
        page: {
          ...this.pageInfo
        }
      }
      return new Promise((resolve, reject) => {
        getListApi({
          ...params
        }).then(res => {
          const { code, data } = res
          if (!code) {
            const { formIdent, formHeadSets, pageData } = data
            this.formIdent = formIdent
            this.columnList = this.initTableHeadMixins(formHeadSets)
            const { list, ...pageInfo } = pageData
            this.initPage(pageInfo)
            this.tableData = list
          }
          this.loading = false
          resolve(res)
        }).catch(err => {
          this.loading = false
          reject(err)
        })
      })
    },
    handleRowClick({ row, column, cell, event }) {
      console.log(row)
      if (column.label === '操作') {
        return false
      }
      this.rightObj = { ...row }
    },
    refresh() {
      // 更新分页
      this.init('clean')
    },
